<style lang="scss" src="./style.scss" scoped></style>
<template src="./template.html"></template>
<script>
export default {
  name: 'character-form',
  props: {
    icon: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      formData: {
        id: 0,
        nickname: '',
        name_CH: '',
        name_JP: '',
        name_KR: '',
        icon: '',
        star: '',
        type: '',
        profession: '',
        recommended: '0',
        cooldown: '',
        LeaderSkill: '',
        BaseSkill: '',
        NormalSkill: '',
        SlideSkill: '',
        DriveSkill: '',
        createDate: '',
        createUser: null
      }
    }
  },
  computed: {
    rowList() {
      return this.$store.state.characters
    },
    dataMap() {
      const map = {}
      this.rowList.forEach(it => {
        map[it.icon] = it
      })
      return map
    },
    rawData() {
      return this.dataMap[this.$props.icon]
    }
  },
  beforeCreate() {
    this.$store.dispatch('CHARACTER_READ')
  },
  watch: {
    rawData() {
      Object.keys(this.rawData).forEach(key => {
        this.formData[key] = this.rawData[key]
      })
    }
  }
}
</script>
