<style lang="scss" src="./style.scss" scoped></style>
<template src="./template.html"></template>
<script>
export default {
  name: 'soul-carta-form',
  props: {
    icon: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      formData: {
        id: 0,
        name_ch: '',
        name_jp: '',
        icon: '',
        hp: 0,
        atk: 0,
        def: 0,
        agl: 0,
        crt: 0
      }
    }
  },
  computed: {
    rowList() {
      return this.$store.state.soulCartaList
    },
    dataMap() {
      const map = {}
      this.rowList.forEach(it => {
        map[it.icon] = it
      })
      return map
    },
    rawData() {
      return this.dataMap[this.$props.icon]
    }
  },
  beforeCreate() {
    this.$store.dispatch('SOUL_CARTA_READ')
  },
  watch: {
    rawData() {
      Object.keys(this.rawData).forEach(key => {
        this.formData[key] = this.rawData[key]
      })
    }
  }
}
</script>
